<template>
  <div>
    <div class="modu-title"><span>商品规格信息</span></div>
    <div class="tyedit-head">商品名称：{{cname}}</div>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="color" label="颜色"></el-table-column>
      <el-table-column prop="size" label="尺寸"></el-table-column>
      <el-table-column prop="sku" label="sku"></el-table-column>
      <el-table-column prop="code" label="用友编码"></el-table-column>
      <el-table-column label="成本价">
        <template scope="scope">
          <el-input v-model="scope.row.chengbingjia" type="number"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="addtime" label="市场价">
        <template scope="scope">
          <el-input v-model="scope.row.marketPx" type="number"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="app销售金额">
        <template scope="scope">
          <el-input v-model="scope.row.saleMoney" type="number"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="inventory" label="库存"></el-table-column>
      <el-table-column label="妙币最大使用量">
        <template scope="scope">
          <el-input v-model="scope.row.maxMiaob" type="number"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="妙币可抵扣金额">
        <template scope="scope">
          <div v-text="scope.row.maxMiaob/100"></div>
        </template>
      </el-table-column>
      <el-table-column label="内页图">
        <template scope="scope">
          <div class="table-buttonbox">
            <el-button size="small" @click="imgpre(scope.$index, scope.row)">预览</el-button>
          </div>
          <div class="table-buttonbox">
            <el-button size="small" @click="imgEdit(scope.$index, scope.row)">编辑</el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="商品详情">
        <template scope="scope">
          <div class="table-buttonbox">
            <el-button size="small" @click="detailPre(scope.$index, scope.row)">预览</el-button>
          </div>
          <div class="table-buttonbox">
            <el-button size="small" @click="detailEdit(scope.$index, scope.row)">编辑</el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button size="small" @click="tySave(scope.$index, scope.row)">保存</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="编辑内页图" :visible.sync="dialogEditimg" size="tiny" center class="dia-detailedit"  @close="dialogEditimg = false">
      <div class="insideimg-box">
        <el-upload action="https://jsonplaceholder.typicode.com/posts/" list-type="picture-card" 
          :multiple=true :file-list="insideimg"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove" :on-success="upsuccess">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImg" alt="">
        </el-dialog>
      </div>
      <div class="insideimg-hint">支持多张图片,图片尺寸：高度不固定，宽度为750</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="inimgSave">修改</el-button>
        <el-button @click="dialogEditimg = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog title="编辑商品详情" :visible.sync="dialogEditd" size="tiny" center class="dia-detailedit"  @close="dialogEditd = false">
      <div class="editor-container">
        <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleSave">修改</el-button>
        <el-button @click="dialogEditd = false">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog :visible.sync="dialogPreimg" size="tiny" class="dia-detailedit"  @close="dialogPreimg = false">
      <el-carousel indicator-position="outside" class="carousel-imgbox">
        <el-carousel-item v-for="(item,index) in diaImgarr" :key="index">
          <img :src="item.url" />
        </el-carousel-item>
      </el-carousel>
    </el-dialog>
    <el-dialog :visible.sync="dialogPred" size="tiny" class="dia-detailedit"  @close="dialogPred = false">
      <div class="dialogPred-con">
        asdfasdf
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPred = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { coCommodityInfo , saveCoCommodity , tyCommodityInfo } from '../../../api/api';
  import UE from '../../../components/ue/ue.vue';
  export default {
    data() {
      return {
        cname:'商品01',
        tableData: [{color:'白色',size:'xxl',sku:'123456',code:'123456',chengbingjia:'8',marketPx:'10',saleMoney:'12',inventory:'100',maxMiaob:'1000'}],
        defaultMsg:'ss',
        insideimg: [],
        dialogImg:'',
        dialogVisible:false,
        config: {
          initialFrameWidth: null,
          initialFrameHeight: 350
        },
        ue1: "ue1",
        dialogPred:false,
        dialogEditd:false,
        dialogPreimg:false,
        dialogEditimg:false,
        diaImgarr:[{url:'http://a.miao.cn/img/content3.jpg',name:''},{url:'http://a.miao.cn/img/content3.jpg',name:''}],
        diaImgdarr:[],
      }
    },
    components: {UE},
    mounted:function(){
      
    },
    methods: {
       getUEContent() {
        let content = this.$refs.ue.getUEContent(); // 调用子组件方法
        this.$notify({
          title: '获取成功，可在控制台查看！',
          message: content,
          type: 'success'
        });
        console.log(content)
      },
      getUEContentTxt() {
        let content = this.$refs.ue.getUEContentTxt(); // 调用子组件方法
        this.$notify({
          title: '获取成功，可在控制台查看！',
          message: content,
          type: 'success'
        });
        console.log(content)
      },
      handleRemove(file,fileList) {
        console.log(fileList);
        this.insideimg=fileList;
      },
      handlePictureCardPreview(file) {
        this.dialogImg = file.url;
        this.dialogVisible = true;
      },
      upsuccess(response, file, fileList) {
        this.insideimg=fileList;
      },
      imgpre(index, row) {
        this.dialogPreimg = true;
      },
      imgEdit(index, row) {
        this.dialogEditimg = true;
        // this.form = Object.assign({}, row);
        // this.table_index = index;
      },
      detailPre(index, row) {
        this.dialogPred=true;
      },
      detailEdit(index, row) {
        this.dialogEditd = true;
      },
      inimgSave() {

      },
      handleSave() {

      },
      tySave(index,row) {
        this.$confirm('确认保存？').then(row => {
          done();
        })
      },
    }
  }
</script>
<style>
  .insideimg-box{
    border:dotted 1px #d7d7d7;
    padding:10px;
  }
  .insideimg-hint{
    font-size:14px;
    line-height: 24px;
    color:#999;
  }
  .modu-title{
    width:100%;
    position:relative;
    height:50px;
    margin-bottom: 22px;
  }
  .modu-title span{
    position:absolute;
    display:inline-block;
    width:500px;
    height:50px;
    left:0;
    top:0;
    text-align: left;
    line-height: 50px;
    font-size:20px;
    color:black;
    font-weight: bold;
  }
  .tyedit-head{
    font-size:16px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  .dia-detailedit .el-dialog--tiny{
    width:800px;
  }
  .common-footer{
    margin-top:10px;
    text-align: center;
  }
  .carousel-imgbox img{
    width:760px;
    height:300px;
  }
  .el-dialog__footer{
    text-align: center;
  }
  .dialogPred-con{
    text-align: center;
  }
</style>


